import { OnInit, Component, ComponentRef} from "@angular/core";
import { ShCtlConst } from "../constShCtl";
import { ShHttpClientConst, ShRedirectService } from 'sh-http-client';

/**
 * 
 */
export class ShAppComponent implements OnInit {

  /**
   * 
   */
  constructor(
    private redirectService: ShRedirectService
  ) {
    this.init();
  }

  /**
   * 
   */
  ngOnInit() {}
  /**
   * 
   */
  private init() {
    this.redirect();
    this.keyValidation();
    this.setToekn();
  }
  private redirect() {
    if(this.redirectService.redirectPath) {
      this.redirectService.redirect();
    }
  }
  private setToekn(){
    const params = new URLSearchParams(location.search);
    const token = params.get('access_token')
    if(token){
      sessionStorage.setItem(`${ShHttpClientConst.AUTHORIZATION_TOKEN}`, token)
      params.delete("access_token");
      history.pushState(null, null, location.pathname);
    }
  }
  /**
   *@ 
   */
  private isTargetElement(elm: HTMLInputElement) {
    if(elm.type == 'hidden') return false;
    if(elm.style.display == 'none') return false;
    if(elm.style.opacity == '0') return false;
    if(elm.disabled) return false;
    const shctl =  this.getShCtlElement(elm);
    if(this.checkShCtlAttr(shctl.attributes,[
      'hidden'
    ])) return false;
    return true;
  }

  private checkShCtlAttr(map: NamedNodeMap, targets: string[]) {
    return targets.map((t) => {
      return map.getNamedItem(t) != null
    }).includes(true);
  }

  private getShCtlElement(elm: HTMLElement): HTMLElement | null {
    let parent = elm.parentElement;
    if(!parent) return null;
    if(parent.tagName.includes('SH-CTL')) {
      return parent;
    } else {
      return this.getShCtlElement(parent);
    }
  }
  /**
   * 
   */
  keyValidation() {
    window.document.onkeydown = (event) => {
      if (
        event.keyCode === ShCtlConst.ORDINAL.KEYS.KEY_F1 ||
        event.keyCode === ShCtlConst.ORDINAL.KEYS.KEY_F2 ||
        event.keyCode === ShCtlConst.ORDINAL.KEYS.KEY_F3 ||
        event.keyCode === ShCtlConst.ORDINAL.KEYS.KEY_F4 ||
        event.keyCode === ShCtlConst.ORDINAL.KEYS.KEY_F5 ||
        event.keyCode === ShCtlConst.ORDINAL.KEYS.KEY_F6 ||
        event.keyCode === ShCtlConst.ORDINAL.KEYS.KEY_F10 ||
        event.keyCode === ShCtlConst.ORDINAL.KEYS.KEY_F11 ||
        event.keyCode === ShCtlConst.ORDINAL.KEYS.KEY_F12
      ) {
        return false;
      }
      if (
        event.altKey &&
        (event.keyCode === ShCtlConst.ORDINAL.KEYS.KEY_LEFT ||
          event.keyCode === ShCtlConst.ORDINAL.KEYS.KEY_RIGHT)
      ) {
        return false;
      }
      if (
        (event.ctrlKey || event.metaKey) &&
        event.keyCode === ShCtlConst.ORDINAL.KEYS.KEY_N
      ) {
        return false;
      }
      if (
        (event.ctrlKey || event.metaKey) &&
        event.keyCode === ShCtlConst.ORDINAL.KEYS.KEY_R
      ) {
        return false;
      }
      if (
        (event.ctrlKey || event.metaKey) &&
        event.keyCode === ShCtlConst.ORDINAL.KEYS.KEY_D
      ) {
        return false;
      }

      if (event.keyCode === ShCtlConst.ORDINAL.KEYS.KEY_ENTER) {
        let input = <HTMLInputElement>document.activeElement;
        if (input != null) {
          if (input.type != 'textarea') {
            //
            const nodeList = window.document.querySelectorAll(
              '.can-moved-with-the-enter-key'
            );
            const filteredInput = Array.from(nodeList).filter((el) => {
              return this.isTargetElement(el as HTMLInputElement)
            })
            let preInput = null;
            for (let i = 0; i < filteredInput.length; i++) {
              let cur_input = <HTMLInputElement>filteredInput[i];
              if (input.name === cur_input.name) {
                if (++i >= filteredInput.length) {
                  i = 0;
                }
                cur_input.blur();
                if (event.shiftKey) {
                  preInput.focus();  
                }
                else {
                  let nextInput = <HTMLInputElement>filteredInput[i];
                  nextInput.focus();  
                }
                return false;
              }
              preInput = <HTMLInputElement>filteredInput[i];
            }
          }
        }
      }
    };
  }
}
