import { Component, OnInit } from '@angular/core';
import { SHCtlCommon } from '../sh-ctl-common';
import { SHFormControl } from '../../utilities/ShFormControl'

@Component({
  selector: 'sh-ctl-half-number',
  templateUrl: './sh-ctl-half-number.component.html',
  styleUrls: ['./sh-ctl-half-number.component.css', '../../../style.common.css']
})
export class SHCtlHalfNumberComponent extends SHFormControl<string> implements OnInit {

  constructor() {
    super();
    this.ctlName = "SHCtlHalfNumber";
  }

  ngOnInit() {
    this.onInit();
  }

  /**
   * onInput
   * @param event input
   */
  public onInput(event: any) {
    SHCtlCommon.replaceFullWidth(event);
    SHCtlCommon.replaceHalfWidth(event);
    if (event.inputType !== 'insertCompositionText') {  
      SHCtlCommon.replaceSpace(event);
      SHCtlCommon.replaceFUllSpace(event);
      SHCtlCommon.replaceSign(event);
      SHCtlCommon.replaceFullWidth(event);
      SHCtlCommon.replaceHalfWidth(event);
    }
  }

  /**
   * onClick
   * @param event click
   */
  public onClick(event) {
    this.onBeforeClick(event);

    this.onAfterClick(event);
  }


  /**
   * onChange
   * @param event change
   */
 public onChange(event) {
  this.onBeforeChange(event);
  
  this.valueChange.emit(event.target.value.replace(/[^\x01-\x7E\xA1-\xDF]/g, ""));

  setTimeout(() => {this.onAfterChange(event)}, 0);
  }

  /**
   * onFocus
   * @param event focus
   */
  public onFocus(event) {
    this.onBeforeFocus(event);

    event.target.select();

    setTimeout(() => {this.onAfterFocus(event)}, 0);
  }

  public onBlur(event) {
    this.onBeforeBlur(event);

    setTimeout(() => {this.onAfterBlur(event);}, 0);;
  }


}
