import { Injectable } from "@angular/core";

@Injectable({
  providedIn: `root`,
})
export class MaxByteLengthService {
  private static readonly allowKeys: string[] = ["ArrowRight", "ArrowLeft", "Backspace", "ArrowDown", "ArrowUp"];
  private static readonly allowCodes: string[] = ["Enter", "Tab"];
  private static readonly allowWithCtlKey: string[] = ["a", "Home", "End"];
  private static readonly allowWithShiftKey: string[] = ["Home", "End"];
  constructor() {}

  public generateByteInfo(value: string) {
    var length = 0;
    let bytes = [];
    if (!value) return { length, bytes };
    for (var i = 0; i < value.length; i++) {
      var c = value.charCodeAt(i);
      if (
        (c >= 0x0 && c < 0x81) ||
        c === 0xf8f0 ||
        (c >= 0xff61 && c < 0xffa0) ||
        (c >= 0xf8f1 && c < 0xf8f4)
      ) {
        length += 1;
        bytes.push(1);
      } else {
        length += 2;
        bytes.push(2);
      }
    }
    return { length, bytes };
  }

  public calcMaxLength(bytes: number[], maxBytelength: number, total: number = 0) {
    let i: number;
    for (i = 0; i < bytes.length; i++) {
      total += bytes[i];
      if (total >= maxBytelength) {
        break;
      }
    }
    return (total - maxBytelength) > 0 ? i : i + 1;
  }
  public static isAllowOperation(e: KeyboardEvent) {
    if (e.ctrlKey && MaxByteLengthService.isAllowWithCtlKey(e.key)) return true;
    if (e.shiftKey && MaxByteLengthService.isAllowWithShiftKey(e.key)) return true;
    if (MaxByteLengthService.isAllowCodes(e.code)) return true;
    if(MaxByteLengthService.isAllowKey(e.key)) return true;
    return false;
  }
  public static isAllowKey(key: string) {
    if (MaxByteLengthService.allowKeys.includes(key)) return true;
    return false;
  }
  private static isAllowCodes(code: string) {
    if (MaxByteLengthService.allowCodes.includes(code)) return true;
    return false;
  }
  private static isAllowWithCtlKey(key: string) {
    if (MaxByteLengthService.allowWithCtlKey.includes(key)) return true;
    return false;
  }
  private static isAllowWithShiftKey(key: string) {
    if (MaxByteLengthService.allowWithShiftKey.includes(key)) return true;
    return false;
  }
}
