import { Component, OnInit } from '@angular/core';
import { SHCtlCommon } from '../sh-ctl-common';
import { SHFormControl } from '../../utilities/ShFormControl'

@Component({
  selector: 'sh-ctl-half-char',
  templateUrl: './sh-ctl-half-char.component.html',
  styleUrls: ['./sh-ctl-half-char.component.css', '../../../style.common.css']
})
export class SHCtlHalfCharComponent extends SHFormControl<string> implements OnInit {

  constructor() {
    super();
    this.ctlName = "SHCtlHalfChar";
  }

  ngOnInit() {
    this.onInit();
  }

  /**
   * onInput
   * @param event input
   */
  public onInput(event) {
    SHCtlCommon.replaceFullWidth(event);
  }

  /**
   * onClick
   * @param event click
   */
  public onClick(event) {
    this.onBeforeClick(event);

    this.onAfterClick(event);
  }


  /**
   * onChange
   * @param event change
   */
 public onChange(event) {
    this.onBeforeChange(event);
    
    this.valueChange.emit(event.target.value);

    setTimeout(() => {this.onAfterChange(event)}, 0);
  }

  /**
   * onFocus
   * @param event focus
   */
  public onFocus(event) {
    this.onBeforeFocus(event);

    event.target.select();

    setTimeout(() => {this.onAfterFocus(event)}, 0);
  }

  /**
   * onBlur
   * @param event blur
   */
  public onBlur(event) {
    this.onBeforeBlur(event);

    event.target.value = !event.target.value ? "" : event.target.value; 
    event.target.value = event.target.value.replace(/^[^\x01-\x7E\uFF61-\uFF9F]+$/g, '');

    setTimeout(() => {this.onAfterBlur(event);}, 0);;
  }
}
