import { Directive, ElementRef, Input, AfterViewInit, OnChanges } from '@angular/core';

@Directive({
  selector: '[readonly]'
})
export class ShReadOnlyDirective implements AfterViewInit, OnChanges {

  constructor(private el: ElementRef) { }

  @Input('readonly') isReadOnly: boolean = true;

  ngAfterViewInit() {
    this.addAttribute();
  }

  ngOnChanges() {
    this.addAttribute();
  }

  addAttribute() {
    let qs = this.el.nativeElement.querySelectorAll('input,textarea,select');
    for (var e of qs) {
      e.readOnly = this.isReadOnly;
    }

    qs = this.el.nativeElement.querySelectorAll('button,mat-icon');
    if (this.isReadOnly) {
      for (var e of qs) {
        e.style.display = "none";
      }
    }
    else {
      for (var e of qs) {
        e.style.display = "inline";
      }
    }
  }
}
