import { Component, OnInit, EventEmitter, Input, Output, OnChanges, SimpleChanges } from "@angular/core";
import { SmplKbnCodeType } from "../../entities/SmplKbnCodeType";
import { SmplKbnCodeService } from "../../services/smpl-kbn-code.service";
import { SHFormControl } from '../../utilities/ShFormControl'

@Component({
  selector: "sh-ctl-kbn-select",
  templateUrl: "./sh-ctl-kbn-select.component.html",
  styleUrls: ["./sh-ctl-kbn-select.component.css", "../../../style.common.css"]
})
export class SHCtlKbnSelectComponent extends SHFormControl<string> implements OnInit {
  @Input() public dataKbnName: string = "";
  @Input() public selectContent: any = "";
  @Input() public apiUrl: string = "/api/";
  @Input() public hiddenClearButton: boolean = false;
  @Output() public throwError = new EventEmitter();

  public name: string = "";
  public event: any;
  public kbnCodes: SmplKbnCodeType[];
  public uniqueStr = "";

  constructor(
    private SmplKbnSelectService: SmplKbnCodeService,
    ) {
    super();
    this.ctlName = "SHCtlKbnSelect";
  }

  /**
   *
   *
   */
  public ngOnInit() {
    this.onInit();
    if (!this.items) {
      if (this.SmplKbnSelectService.getMasterData(this.apiUrl).length == 0) {
        this.SmplKbnSelectService.fetchAll(this.apiUrl).then(response => {
          this.kbnCodes = response;
          this.SmplKbnSelectService.setMasterData(this.apiUrl, response);
        });
      } else {
        if (this.SmplKbnSelectService.getMasterData(this.apiUrl).length > 0)
          this.kbnCodes = this.SmplKbnSelectService.getMasterData(this.apiUrl);
      }
      if (this.SmplKbnSelectService.getMasterData(this.apiUrl).length > 0)
        this.kbnCodes = this.SmplKbnSelectService.getMasterData(this.apiUrl);
    } else {
      this.kbnCodes = this.items;
    }
  }

  /**
   *items
   *items
   */
  ngOnChanges(change: SimpleChanges) {
    if (change.items) {
      if (change.items.currentValue) {
          this.kbnCodes = change.items.currentValue;
      }
    }
  }

  /**
   * onClick
   * @param event click
   */
  public onClick(event) {
    this.onBeforeClick(event);

    this.onAfterClick(event);
  }

  /**
   * onChange
   * @param event change
   */
  public onChange() {
    this.onBeforeChange(event);
    
    this.valueChange.emit(this.value);

    setTimeout(() => {this.onAfterChange(event)}, 0);
  }

  /**
   * onFocus
   * @param event focus
   */
  public onFocus(event) {
    this.onBeforeFocus(event);

    setTimeout(() => {this.onAfterFocus(event)}, 0);
  }

  /**
   * onBlur
   * @param event blur
   */
  public onBlur(event) {
    this.onBeforeBlur(event);

    setTimeout(() => {this.onAfterBlur(event);}, 0);;
  }

  /**
   * 
   * @param value this.value
   */
  public changeSelectContent(item) {
    this.selectContent = item;
  }

  /**
   * 
   */
  public clearValue() {
    this.value = null;
    this.onChange();
  }

}
