import { Pipe, PipeTransform } from '@angular/core';

//Datesh-ctl-date
@Pipe({
  name: 'ngDateValue'
})
export class NgDateValuePipe implements PipeTransform {
  transform(value: Date): any {
    if (value != null) {
      return {
        year: value.getFullYear(),
        month: value.getMonth() + 1,
        day: value.getDate()
      };
    }
    return null;
    
  }
}
