import { Injectable } from "@angular/core";
import { ShCtlConst } from "../constShCtl";
import { NoChangeFlg } from '../types/NoChangeFlg'

type Param = {
  bindColumnArray: { [key: string]: string }[];
  record: any;
};

@Injectable({
  providedIn: `root`,
})
export class BindCtlService {

  private readonly NO_CHANGE_FLG_SUFFIX = ShCtlConst.NO_CHANGE.FLG_SUFFIX;

  constructor() {}

  public setNoChangeFlg(param: Param) {
    if (this.isParamEmpty(param)) return;
    const info = this.generateBindInputInfo(param);
    info.forEach((data) => {
      const targetInput = document.querySelector(
        `input[name=${data.ctlName}_${this.NO_CHANGE_FLG_SUFFIX}],textarea[name=${data.ctlName}_${this.NO_CHANGE_FLG_SUFFIX}]`
      ) as HTMLInputElement;
      if(!targetInput) return;
      targetInput.value = NoChangeFlg.ON;
      targetInput.dispatchEvent(new Event(`change`));
    });
  }

  public setBindValue(param: Param) {
    if (this.isParamEmpty(param)) return;
    const info = this.generateBindInputInfo(param);
    info.forEach((data) => {
        const targetInput = document.querySelector(
          `input[name=${data.ctlName}],textarea[name=${data.ctlName}]`
        ) as HTMLInputElement;
        if(!targetInput) return;
        targetInput.value = data.value;
        targetInput.dispatchEvent(new Event(`change`));
      });
  }

  private generateBindInputInfo(
    param: Param
  ): { ctlName: string; value: string }[] {
    return param.bindColumnArray.map((data) => {
      const column = Number(Object.values(data)[0]);
      return {
        ctlName: Object.keys(data)[0],
        value: isNaN(Number(column)) ? `` : Object.values(param.record)[column - 1] as string,
      };
    });
  }

  private isParamEmpty(param: Param) {
    if (!param.bindColumnArray || param.bindColumnArray.length == 0)
      return true;
    if (!param.record) return true;
    return false;
  }
}
