import { Injectable } from '@angular/core';
import { ShCtlApiService } from './sh-ctl-api.service';
import { Subject } from 'rxjs';

/**
 * 
 */
@Injectable({
  providedIn: 'root'
})
export class SmplPopupService {
  private popupCdSubject = new Subject<string>();
  public popupCdState = this.popupCdSubject.asObservable();
  constructor(
    private apiComService: ShCtlApiService,
  ) { }

  //
  private masterCache: any[] = [];

  /**
   * 
   */
  public async fetchAll(url: string): Promise<any> {
    return this.apiComService.get<any[]>(url);

  }

  /**
   * 
   * @param key 
   */
  public getMasterData(key: string): any[] {
    let cache = this.masterCache.filter(item => {
      return item.masterName == key
    })
    return cache.length > 0 ? cache[0].masterCache : [];
  }

  /**
   * 
   * @param key 
   * @param masterData 
   */
  public setMasterData(key: string, masterData: any[]) {
    if (this.masterCache.length > 0) {
      for (let item of this.masterCache) {
        if (item.masterName == key) {
          item.masterCache = masterData
          break;
        }
      }
    }
    this.masterCache = this.masterCache.concat([{ masterName: key, masterCache: masterData }]);
  }
}
