import { Component, OnInit } from '@angular/core';
import { SHCtlCommon } from '../sh-ctl-common';
import { SHFormControl } from '../../utilities/ShFormControl'

@Component({
  selector: 'sh-ctl-bunj-no',
  templateUrl: './sh-ctl-bunj-no.component.html',
  styleUrls: ['./sh-ctl-bunj-no.component.css', '../../../style.common.css']
})
export class SHCtlBunjNoComponent extends SHFormControl<string> implements OnInit {

  constructor() {
    super();
    this.ctlName = "SHCtlBunjNo";
    this.maxlength = 9;
  }

  ngOnInit() {
    this.onInit();
  }

  /**
   * onInput
   * @param event input
   */
  public onInput(event) {
    SHCtlCommon.replaceSpace(event);
    SHCtlCommon.replaceFullWidth(event);
    SHCtlCommon.replaceHalfWidth(event);
    SHCtlCommon.replaceSign(event);
    SHCtlCommon.sliceLength(event, this.maxlength);
  }

  /**
   * onClick
   * @param event click
   */
  public onClick(event) {
    this.onBeforeClick(event);

    this.onAfterClick(event);
  }

  /**
   * onChange
   * @param event change
   */
  public onChange(event) {
    this.onBeforeChange(event);

    event.target.value = this.toText(event.target.value);
    this.valueChange.emit(event.target.value);

    setTimeout(() => {this.onAfterChange(event)}, 0);
  }

  /**
   * onFocus
   * @param event focus
   */
  public onFocus(event) {
    this.onBeforeFocus(event);

    event.target.value = this.toValue(event.target.value)
    event.target.select();

    setTimeout(() => {this.onAfterFocus(event)}, 0);
    return true;

  }

  /**
   * onBlur
   * @param event blur
   */
  public onBlur(event) {
    this.onBeforeBlur(event);

    event.target.value = this.toText(event.target.value);

    setTimeout(() => {this.onAfterBlur(event);}, 0);;
  }

  /**
   * 
   * @param inputValue input
   */
  public toText(inputValue: string): string {
    // 
    if (SHCtlCommon.isBlank(inputValue)) return "";
    if (inputValue.length > 9) return inputValue;
    inputValue = inputValue.replace(/[^\x01-\x7E\xA1-\xDF]/g, "");
    const result: string = (('00000000' + inputValue).slice(-9));
    return result;
  }

  /**
   * 
   * @param inputValue input
   */
  public toValue(inputValue: string): string {
    if (!inputValue) return "";
    return inputValue.replace(/^0*/, "");
  }

}
