
import { Component, OnInit } from '@angular/core';
import { SHCtlCommon } from '../sh-ctl-common';
import { SHFormControl } from '../../utilities/ShFormControl'

@Component({
  selector: 'sh-ctl-katakana',
  templateUrl: './sh-ctl-katakana.component.html',
  styleUrls: ['./sh-ctl-katakana.component.css', '../../../style.common.css']
})
export class SHCtlKatakanaComponent extends SHFormControl<string> implements OnInit {

  //
  private _data ="";

  constructor() {
    super();
    this.ctlName = "SHCtlKatakana";
  }

  ngOnInit() {
    this.onInit();
  }
  
  /**
   * onInput
   * @param event input
   */
  public onInput(event) {
    const isSameData = (this._data == event.data);
    this._data = event.data
    //
    if(event.isComposing && (!event.isComposing || !isSameData))
      return;
    if ( !event.isComposing ) {
      if (this.maxByteLength > 0)
        if (event.inputType !== 'insertCompositionText') {
          this.onKeyDown(event);
          SHCtlCommon.sliceLength(event, this.maxByteLength);
        }
    }
  }

  /**
   * onPaste
   * @param event paste
   */
  public onPaste(event) {
    let obj = this;
    setTimeout(function(e){
      SHCtlCommon.replaceHalfDoubleQuote(event);
      obj.replaceFullWIdth(event);
    }, 100);
  }

  /**
   * onClick
   * @param event click
   */
  public onClick(event) {
    this.onBeforeClick(event);

    this.onAfterClick(event);
  }

  /**
   * onFocus
   * @param event focus
   */
  public onFocus(event) {
    this.onBeforeFocus(event);

    event.target.select();

    setTimeout(() => {this.onAfterFocus(event)}, 0);
  }

  /**
   * onChange
   * @param event chage
   */
  public onChange(event) {
    this.onBeforeChange(event);

    //emit
    this.onInput(event);
    this.replaceFullWIdth(event);
    this.valueChange.emit(event.target.value);

    setTimeout(() => {this.onAfterChange(event)}, 0);
  }

  /**
   * onBlur
   * @param event blur
   */
  public onBlur(event) {
    this.onBeforeBlur(event);
    this.replaceFullWIdth(event)
    setTimeout(() => {this.onAfterBlur(event);}, 0);;
  }

  public onKeyUp(event) {
    if ( !event.isComposing ) { 
      SHCtlCommon.replaceHalfDoubleQuote(event);
      this.replaceFullWIdth(event);
    }
  }

  /**
   * onKeyDown
   * @param event KeyDown
   */
  public onKeyDown(event) {
    super.onKeyDown(event);
  }

 /**
  * 
  * @param event
  */
  public replaceFullWIdth(event) {
    event.target.value = event.target.value.replace(/[--\u30e0-\u9fcf---]/g, "");
  }

}
