import {
  Component,
  OnInit,
  OnChanges,
  Input,
} from "@angular/core";
import { SHCtlCommon } from "../sh-ctl-common";
import { MatDialog } from "@angular/material";
import { SHFormPopup } from "../../utilities/ShFormPopup";
import { SmplPopupService } from "../../services/smpl-popup.service";
import { SHCtlPopupDialogComponent } from "../sh-ctl-popup-dialog/sh-ctl-popup-dialog.component";
import { ShCtlConst } from "../../constShCtl";
import { BindCtlService } from "../../services/bind-ctl.service";
import { LockScreenService } from "../../services/lock-screen.service";

@Component({
  selector: "sh-ctl-jyu-code",
  templateUrl: "./sh-ctl-jyu-code.component.html",
  styleUrls: ["./sh-ctl-jyu-code.component.css", "../../../style.common.css"],
})
export class SHCtlJyuCodeComponent
  extends SHFormPopup
  implements OnInit, OnChanges {
  @Input() enabledHalfKanaInput: boolean = true;
  public obj = ".SHCtlJyuCode";

  constructor(
    protected smplPopupService: SmplPopupService,
    protected dialog: MatDialog,
    protected bindCtlService: BindCtlService,
    protected lockScreenService: LockScreenService
  ) {
    super(smplPopupService, dialog, bindCtlService, lockScreenService);
    this.ctlName = "SHCtlJyuCode";
    this.maxlength = 6;
  }

   /**
   * 
   * 
   */
  public ngOnInit() {
    this.onInit();
    if (this.popupWidth) {
      this.extendWidth = this.popupWidth;
    }
  }

  /**
   * onInput
   * @param event input
   */
  public onInput(event) {
    event.target.value = event.target.value.replace(/[^0-9]/g, "");
    SHCtlCommon.sliceLength(event, this.maxlength);
    if (!this.enabledHalfKanaInput) {
      SHCtlCommon.replaceFullWidth(event);
    }
    event.target.value = event.target.value.replace(
      /[""]/g,
      ""
    );
    if (
      event.target.value.length >= 6 &&
      event.inputType != "deleteContentBackward"
    ) {
      if (
        isNaN(parseInt(event.target.value)) &&
        event.target.value.length != 7
      ) {
        return;
      }
      return;
    }
    if (event.inputType !== "insertCompositionText") {
      SHCtlCommon.replaceSpace(event);
      SHCtlCommon.replaceSign(event);
    }
  }

  /**
   * onChange
   * @param event change
   */
  public onChange(event) {
    this.onBeforeChange(event);
    let flg = false;
    if (this.noChangeFlg == `OFF`) {
      flg = true;
      if (
        this.SmplPopupService.getMasterData(this.apiUrl).length == 0 &&
        event.target.value
      ) {
        this.fetchCodes(this.apiUrl).then((value) => {
          this.valueChange.emit(value);
          event.target.value = this.toText(event.target.value);
          setTimeout(() => {
            this.onAfterChange(event);
          }, 0);
        });
      } else {
        this.codes = Object(
          this.SmplPopupService.getMasterData(this.apiUrl)
        ).data;
        event.target.value = this.setName(event.target.value);
        this.valueChange.emit(event.target.value);
        setTimeout(() => {
          this.onAfterChange(event);
        }, 0);
      }
    }
    if (!flg) {
      setTimeout(() => {
        // this.noChangeFlg == `OFF`;
        setTimeout(() => {
          this.noChangeFlg = `OFF`;
        }, 1000);
        this.valueChange.emit(event.target.value);
        this.onAfterChange(event);
        event.target.value = this.toText(event.target.value);
      }, 0);
    }
  }

  /**
   * 
   */
  public openDialog() {
    let extendWidth;
    if (this.codes) {
      if (
        !this.popupWidth &&
        this.codes.length != 0 &&
        Object.values(this.codes[0]).length >= 4
      ) {
        this.extendWidth = ShCtlConst.DIALOG_MAX_WIDTH;
      }

      let rawCodes;
      let rawCodesArray = [];
      for (let i = 0; this.codes.length > i; i++) {
        rawCodes = this.codes[i].contactCode;
        rawCodesArray.push(rawCodes);
        let code = Object.values(this.codes[i])
          [this.bindColumnKey - 1].toString()
          .substr(2);
        this.codes[i].contactCode = code;
      }
      this.dialogRef = this.dialog.open(SHCtlPopupDialogComponent, {
        height: ShCtlConst.DIALOG_HEIGHT,
        width: extendWidth,
        data: {
          dataTitle: this.dataTitle,
          codes: this.codes,
          codesHeader: this.codesHeader,
          hiddenColumns: this.hiddenColumns,
        },
      });

      this.dialogRef.afterClosed().subscribe((result) => {
        if (result) {
          this.setValueName(result);
          this.bindCtlService.setNoChangeFlg({
            bindColumnArray: this.bindColumnArray,
            record: result,
          });
          this.bindCtlService.setBindValue({
            bindColumnArray: this.bindColumnArray,
            record: result,
          });
          this.noChangeFlg = `ON`;
          this.valueChange.emit(this.extractBindValue(result));
          this.ownValueInputElm.dispatchEvent(new Event("change"));
        }
      });
    }
  }

  /**
   * 
   */
  public setName(val) {
    let record: any;
    val = this.toText(val);
    if (val.length == 6) {
      record = this.codes.find((element) =>
        this.toText(Object.values(element)[this.bindColumnKey - 1]).includes(
          val
        )
      );
    }
    if (record != null) {
      const name = Object.values(record)[this.bindColumnValue - 1] as string;
      if (name && val.length == 6) {
        this.setValueName(record);
        this.noChangeFlg = "ON";
        this.bindCtlService.setBindValue({
          bindColumnArray: this.bindColumnArray,
          record,
        });
        this.bindCtlService.setNoChangeFlg({
          bindColumnArray: this.bindColumnArray,
          record,
        });
        val = Object.values(record)[this.bindColumnKey - 1];
        const inputElement: HTMLInputElement = this.ownValueInputElm;
        if (inputElement) {
          setTimeout(() => {
            inputElement.value = this.toText(val);
          }, 0);
        }
      }
    } else {
      val = ``;
    }

    return val;
  }
  public checkValue(value) {
    if (!value) return false;
    if(value.length < 6) {
      this.clearValue();
      return false;
    }
    //let matchedArray = [];
    if (this.codes[0]) {
      const filterResult = this.codes.filter((element) =>
        //this.hasMatchRecords(element, value)
        (Object.values(element)[this.bindColumnKey - 1] as string).includes(
          value
        )
      );
      if (filterResult.length == 0) {
        this.clearValue();
        return false;
      }

      if (filterResult.length >= 2) {
        alert(value + "");
        this.clearValue()
        this.valueChange.emit("");
        return false;
      }
    }
    return true;
  }
  /**
   * onClick
   * @param event click
   */
  public onClick(event) {
    this.onBeforeClick(event);

    this.onAfterClick(event);
  }

  /**
   * onFocus
   * @param event focus
   */
  public onFocus(event) {
    this.onBeforeFocus(event);

    event.target.value = this.toText(event.target.value);
    event.target.select();

    setTimeout(() => {
      this.onAfterFocus(event);
    }, 0);
  }

  /**
   * onBlur
   * @param event blur
   */
  public onBlur(event) {
    this.onBeforeBlur(event);
    
    setTimeout(() => {
      this.onAfterBlur(event);
    }, 0);
  }

  /**
   * onKeyUp
   * @param event KeyUp
   */
  public onKeyUp(event) {
    if (!event.isComposing) {
      event.target.value = this.toHankaku(
        this.hiraganaToKatagana(event.target.value)
      );
    }
  }

  /**
   * 
   * @param inputTargetValue event.target.value
   */
  public toText(inputTargetValue) {
    if (SHCtlCommon.isBlank(inputTargetValue)) {
      return "";
    }
    if (inputTargetValue.length === 8) {
      return inputTargetValue.substr(2);
    }
    return inputTargetValue;
  }

  /**
   * 
   * @param inputValue
   */
  public hiraganaToKatagana(inputValue) {
    return inputValue.replace(/[\u3041-\u3096]/g, function (match) {
      var chr = match.charCodeAt(0) + 0x60;
      return String.fromCharCode(chr);
    });
  }

  /**
   * 
   * @param inputValue
   */
  public toHankaku(inputValue) {
    let half = "0123456789";
    half += "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
    half += "abcdefghijklmnopqrstuvwxyz";
    half += "\\;:-+|_@., ";
    half += "";
    let halfArr = new Array(
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      ""
    );

    let full = "";
    full += "";
    full += "";
    full += "";
    full +=
      "";
    let fullArr = new Array(
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      ""
    );

    let str = "";
    //
    for (let i = 0; i < halfArr.length; i++) {
      let reg = new RegExp(fullArr[i], "g");
      inputValue = inputValue.replace(reg, halfArr[i]);
    }
    for (let i = 0; i < inputValue.length; i++) {
      let oneStr = inputValue.charAt(i);
      let num = full.indexOf(oneStr, 0);
      oneStr = num >= 0 ? half.charAt(num) : oneStr;
      str += oneStr;
    }
    return str;
  }
}
