import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { ValidationErrorService } from 'sh-http-client';
import { Subscription } from 'rxjs';

@Component({
  selector: 'sh-ctl-validated-error',
  templateUrl: './sh-ctl-validated-error.component.html',
  styleUrls: ['./sh-ctl-validated-error.component.css']
})
export class ShCtlValidatedErrorComponent implements OnInit, OnDestroy {
  @Input() message: string;
  @Input() isError: boolean = false;
  @Input() errors: string[];
  private subscription: Subscription;
  /**
   * 
   */
  constructor(
    private validationErrorService: ValidationErrorService
  ) { }

  /**
   * 
   */
  public ngOnInit() {
    this.subscription = this.validationErrorService.validationErrorState
      .subscribe(response => {
        if (response.show) {
          this.isError = true;
          this.errors = [];
          Object.keys(response.meesages).forEach(controlName => {
            this.errors = [...this.errors, ...response.meesages[controlName]];
          });
        } else {
          this.isError = false;
          this.errors = [];
        }
      });
  }
  public ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}
