import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { SHFormControl } from '../../utilities/ShFormControl'

@Component({
  selector: 'sh-ctl-check',
  templateUrl: './sh-ctl-check.component.html',
  styleUrls: ['./sh-ctl-check.component.css', '../../../style.common.css']
})

export class SHCtlCheckComponent extends SHFormControl<string> implements OnInit, OnChanges {

  //
  @Input() public checkedLabel: string = "";  
  //()
  @Input() public beforeAlign: boolean = false;
  //
  @Input() public checkedValue: string = "1";
  //
  @Input() public uncheckedValue: string = "0";

  constructor(  ) {
    super();
    this.ctlName = "SHCtlCheck";
  }

  public setLabelPosition() {
    return this.beforeAlign ? 'before' : 'after';
  }

  ngOnInit() {
    this.onInit();
  }

  /**
   * ngOnChanges
   */
  public ngOnChanges(changes: SimpleChanges) {
    if (changes.value) {
      if (changes.value.currentValue) {
        if (changes.value.currentValue === '' || changes.value.currentValue === undefined || changes.value.currentValue === String(/[^\x01-\x7E\xA1-\xDF]+$/g)) {
          this.value = '';
          return;
        }
        this.value = changes.value.currentValue == this.checkedValue ? "checked" : "";
      }
    }
  }

  /**
   * onClick
   * @param event click
   */
  public onClick(event) {
    this.onBeforeClick(event);

    this.onAfterClick(event);
  }

  /**
   * onChange
   * @param event change
   */
  public onChange(event) {
    this.onBeforeChange(event);

    this.valueChange.emit(event.checked ? this.checkedValue :  this.uncheckedValue);

    setTimeout(() => {this.onAfterChange(event)}, 0);
  }

  /**
   * onFocus
   * @param event focus
   */
  public onFocus(event) {
    this.onBeforeFocus(event);

    event.target.select();

    setTimeout(() => {this.onAfterFocus(event)}, 0);
  }

  /**
   * onBlur
   * @param event blur
   */
  public onBlur(event) {
    this.onBeforeBlur(event);

    setTimeout(() => {this.onAfterBlur(event);}, 0);;
  }

}
