import { Injectable } from '@angular/core';
import { ShHttpClientService, ApiResponseBody } from 'sh-http-client';
import { ShCtlConst } from '../constShCtl';

/**
 * ShCtlApi
 */
@Injectable({
  providedIn: 'root'
})
export class ShCtlApiService {

  constructor(
    private apiComService: ShHttpClientService,
  ) { }

  /**
  * APIApplicationData
  * @param apiResponse API
  * @return APIApplicationData
  */
  private getApplicationData<T>(apiResponseBody: ApiResponseBody<T>): T {
    //
    //
    //
    return apiResponseBody.applicationData;

  }

  /**
  * GET
  * @param url ApiUrl
  * @return API
  */
  public async get<T>(url: string): Promise<T> {
    let ret: T = null;
    await this.apiComService.get<T>(url, false)
      .then(response => {
        ret = this.getApplicationData(response);
      });
    return ret;

  }

  /**
  * POST
  * @param url ApiUrl
  * @param data 
  * @return API
  */
  public async post<T>(url: string, data: any): Promise<T> {
    let ret: T = null;
    await this.apiComService.post<T>(url, data)
      .then(response => {
        ret = this.getApplicationData(response);
      });
    return ret;

  }

  /**
  * PUT
  * @param url ApiUrl
  * @param data 
  * @return API
  */
  public async put<T>(url: string, data: any): Promise<T> {
    let ret: T = null;
    await this.apiComService.put<T>(url, data)
      .then(response => {
        ret = this.getApplicationData(response);
      });
    return ret;

  }

  /**
  * DELETE
  * @param url ApiUrl
  * @return API
  */
  public async delete<T>(url: string): Promise<T> {
    let ret: T = null;
    await this.apiComService.delete<T>(url)
      .then(response => {
        ret = this.getApplicationData(response);
      });
    return ret;

  }
}
