import { Component, OnInit } from '@angular/core';
import { LockScreenService } from '../../services/lock-screen.service';

@Component({
  selector: 'sh-ctl-lock-screen',
  templateUrl: './sh-ctl-lock-screen.component.html',
  styleUrls: ['./sh-ctl-lock-screen.component.css']
})
export class SHCtlLockScreenComponent implements OnInit {
  public isConnecting: boolean = false;

  /**
  * 
  */
  constructor(
    private lockScreen: LockScreenService
  ) { }

  /**
  * 
  */
  public ngOnInit() {
    this.lockScreen.isConnectingState.subscribe(
      isConnecting => {
        this.isConnecting = isConnecting;
      }
    );

  }

}
