import { Component, OnInit } from '@angular/core';
import { SHCtlCommon } from '../sh-ctl-common';
import { SHFormControl } from '../../utilities/ShFormControl'

@Component({
  selector: 'sh-ctl-wide-char-ml',
  templateUrl: './sh-ctl-wide-char-ml.component.html',
  styleUrls: ['./sh-ctl-wide-char-ml.component.css', '../../../style.common.css']
})
export class SHCtlWideCharMLComponent extends SHFormControl<string> implements OnInit {

  //
  private _data ="";
  private allowkeys = [`ArrowLeft`, `ArrowRight`, `ArrowDown`, `ArrowUp`];

  constructor() {
    super();
    this.ctlName = "SHCtlWideCharML";
  }
  private isAllowkeys(key: string) {
    return this.allowkeys.includes(key);
  }
  public ngOnInit() {
    this.onInit();
    if (this.maxByteLength % 2 != 0)
      this.maxByteLength = this.maxByteLength - 1;
  }

  /**
   * onInput
   * @param event input
   */
  public onInput(event) {
    const isSameData = (this._data == event.data);
    this._data = event.data;
    //
    if(event.isComposing && (!event.isComposing || !isSameData))
      return;

    if (event.inputType !== 'insertCompositionText') {
      SHCtlCommon.sliceLength(event, this.maxlength);
    }
  }

  /**
   * onClick
   * @param event click
   */
  public onClick(event) {
    this.onBeforeClick(event);

    this.onAfterClick(event);
  }

  /**
   * onFocus
   * @param event focus
   */
  public onFocus(event) {
    this.onBeforeFocus(event);

    event.target.select();

    setTimeout(() => {this.onAfterFocus(event)}, 0);
  }

  /**
   * onChange
   * @param event change
   */
  public onChange(event) {
    this.onBeforeChange(event);

    //emit
    this.onInput(event);
    this.onKeyUp(event);
    this.valueChange.emit(event.target.value);

    setTimeout(() => {this.onAfterChange(event)}, 0);
  }

  /**
   * onBlur
   * @param event blur
   */
  public onBlur(event) {
    this.onBeforeBlur(event);

    if (event.target.value == undefined) {
      setTimeout(() => {this.onAfterBlur(event);}, 0);;
      return;
    };

    setTimeout(() => {this.onAfterBlur(event);}, 0);;
  }

  /**
   * onKeyUp
   * @param event KeyUp
   */
  public onKeyUp(event) {
    const input = event.target as HTMLInputElement;
    const cursor = input.selectionStart;
    if ( !event.isComposing ) { 
      SHCtlCommon.replaceHalfSpaceToFull(event);
      SHCtlCommon.replaceFullDoubleQuote(event);
      SHCtlCommon.replaceHalfKana(event);
      if(event.shiftKey || event.key === "Shift") return;
      input.setSelectionRange(cursor, cursor);
    }
  }

  /**
   * onKeyDown
   * @param event KeyUp
   */
  public onKeyDown(event) {
    if (this.maxByteLength > 0)
      super.onKeyDown(event);
  }

}
