import { Component, OnInit, Input } from '@angular/core';
import { SHCtlCommon } from '../sh-ctl-common';
import { SHFormControl } from '../../utilities/ShFormControl'

@Component({
  selector: 'sh-ctl-time',
  templateUrl: './sh-ctl-time.component.html',
  styleUrls: ['./sh-ctl-time.component.css', '../../../style.common.css']
})
export class SHCtlTimeComponent extends SHFormControl<string> implements OnInit {
  public obj: string = ".ShCtlTime";
  public adjust: number = 1;
  public isValidateError: boolean = false;
  public errorMessage: string = "";
  public stringValue: any;
  public isFirst: boolean = true;
  @Input() public format: number = SHCtlCommon.Const.timeFormat.HHMM;

  //
  private timeStrage: string = null;

  constructor(  ) {
    super();
    this.ctlName = "SHCtlTime";
  }

  public ngAfterContentChecked() {
    if (this.value != null) {
      var date = this.value.split(':');
      switch(this.format){
        case SHCtlCommon.Const.timeFormat.HH:
          this.stringValue = `${date[0]}`
          break;
        case SHCtlCommon.Const.timeFormat.HHMM:
          this.stringValue = `${date[0]}:${(date.length > 1 ? date[1] : "00")}`
          break;
        case SHCtlCommon.Const.timeFormat.HH:
          this.stringValue = `${date[0]}:${(date.length > 1 ? date[1] : "00")}:${(date.length > 2 ? date[2] : "00")}`
          break;
        default:
          break;
      }
    }
  }

  public ngOnInit() {
    this.onInit();
  }

  /**
   * onInput
   * @param event input
   */
  public onInput(event: any) {
    this.timecalculation(event);
  }

  /**
   * onClick
   * @param event click
   */
  public onClick(event) {
    this.onBeforeClick(event);

    this.onAfterClick(event);
  }

  /**
   * onFocus
   * @param event focus
   */
  public onFocus(event) {
    this.onBeforeFocus(event);

    event.target.select();

    setTimeout(() => {this.onAfterFocus(event)}, 0);
  }

  /**
   * onBlur
   * @param event blur
   */
  public onBlur(event) {
    this.onBeforeBlur(event);

    let val = event.target.value
    if (val) {
      //HH
      if (val.length == 2)
        val += ":00:00";
      //HHMM
      if (val.length == 5)
        val += ":00";
    }

    if (this.timeStrage != val) {
      this.onBeforeChange(event);
      this.valueChange.emit(val);
    }
    this.timeStrage = val;
    setTimeout(() => {this.onAfterBlur(event)}, 0);
  }

  /**
   * onKeypress
   * @param event keypress
   */
  public onKeypress(event) {
    this.timecalculation(event);
  }

  /**
   * onChange
   * @param event change
   */
  public onChange(event) {

  }

  /**
   * 
   * /
   * @param event keypress
   */
  private timecalculation(event) {
    if (event.key === SHCtlCommon.Const.keyCodes.signs[191]) {
      event.target.value = this.setCurrentTime();
      this.onChange(event);
    }
  }

  /**
   * 
   * @param event keypress
   */
  private setCurrentTime() {
    let newDate = new Date();
    let nullFirstString = null;
    let nullLastString = null;
    nullFirstString = SHCtlCommon.padding(newDate.getHours(), 2); // HH
    nullLastString = SHCtlCommon.padding(newDate.getMinutes(), 2) // HH:MM
    let stringValue = String(nullFirstString + ":" + nullLastString);
    return stringValue;
  }

}
