import { Input, SimpleChanges } from "@angular/core";
import { SHCtlCommon } from "../components/sh-ctl-common";
import { ShCtlConst } from "../constShCtl";
import { SmplPopupService } from "../services/smpl-popup.service";
import { MatDialog, MatDialogRef } from "@angular/material";
import { SHFormControl } from "./ShFormControl";
import { SHCtlPopupDialogComponent } from "../components/sh-ctl-popup-dialog/sh-ctl-popup-dialog.component";
import { BindCtlService } from "../services/bind-ctl.service";
import { LockScreenService } from "../services/lock-screen.service";
import { NoChangeFlg } from "../types/NoChangeFlg";


export class SHFormPopup extends SHFormControl<any> {
  //
  @Input() public dataTitle: string = "";
  //
  @Input() public dataKbnName: string = "";
  //API URL
  @Input() public apiUrl: string = "/api/";
  //
  @Input() public bindColumnKey: number = 1;
  //
  @Input() public bindColumnValue: number = 2;
  //
  @Input() public ctlName;
  //
  @Input() public popupLabelName;
  //
  @Input() public bindColumnArray: any;
  //
  @Input() public hiddenInput: boolean = false;
  //
  @Input() public hiddenLabel: boolean = false;
  //
  @Input() public hiddenPopupButton: boolean = false;
  //ON/OFF
  @Input() public dataCache: boolean = false;
  //
  @Input() public hiddenColumns: number[] = [];
  // 
  @Input() public popupWidth: string;

  public name: string = "";
  public event: any;
  public codes = [];
  public codesHeader = [];

  public extendWidth = ShCtlConst.DIALOG_WIDTH;

  //Dialog
  public dialogRef: MatDialogRef<SHCtlPopupDialogComponent, any> = null;
  // 
  public initFetchFlg: boolean = false;
  // bindColumnArray
  get noChangeFlgCtlName(): string {
    return `${this.ctlName}_${ShCtlConst.NO_CHANGE.FLG_SUFFIX}`;
  }
  // input
  get ownValueInputElm(): HTMLInputElement | null {
    return (
      <HTMLInputElement>(
        document.querySelector(
          `textarea[name=${this.ctlName}], input[name=${this.ctlName}]`
        )
      ) || null
    );
  }
  get ownLabelInputElm(): HTMLInputElement | null {
    return <HTMLInputElement>(
      (document.querySelector(
        `textarea[name=${this.popupLabelName}], input[name=${this.popupLabelName}]`
      ) || null)
    );
  }

  // bindColumnArray
  public noChangeFlg: NoChangeFlg = `OFF`;
  // 
  public isBusy = false;
  constructor(
    protected SmplPopupService: SmplPopupService,
    protected dialog: MatDialog,
    protected bindCtlService: BindCtlService,
    protected lockScreenService: LockScreenService
  ) {
    super();
    this.ctlName = "SHCtlPopup";
  }

  /**
   *
   */
  public noChangeFlgChange(event) {
    this.noChangeFlg = event.target.value;
    setTimeout(() => {
      this.noChangeFlg = `OFF`;
    }, 1000);
  }
  /**
   * 
   */
  public setInputWidth() {
    return this.hiddenLabel ? { width: "100%" } : { width: "33.3%" };
  }

  /**
   * 
   */
  public setLabelWidth() {
    return this.hiddenInput ? { width: "100%" } : { width: "66.6%" };
  }

  /**
   * 
   * 
   * @param changes 
   */
  public ngOnChanges(changes: SimpleChanges) {
    if (changes.value && !changes.value.currentValue) {
      this.name = "";
      this.value = "";
      return;
    }
    if (this.noChangeFlg == "OFF") {
      if (changes.value && changes.value.currentValue) {
        if (changes.value.currentValue != changes.value.previousValue) {
          this.fetchCodes(this.apiUrl);
          return;
        }
      }
      if (changes.value && changes.value.firstChange) {
        this.initFetchFlg = true;
      }

      if (this.initFetchFlg) {
        if (!this.value || !changes.value.currentValue) return;
        this.fetchCodes(this.apiUrl);
        this.initFetchFlg = false;
      }
      if (
        changes.apiUrl &&
        changes.apiUrl.previousValue != changes.apiUrl.currentValue &&
        !this.isBindInputNull(this.bindColumnArray)
      ) {
        if (
          this.SmplPopupService.getMasterData(changes.apiUrl.currentValue)
            .length == 0
        ) {
          if (
            changes.value &&
            changes.value.currentValue == changes.value.previousValue
          )
            return;
          this.fetchCodes(changes.apiUrl.currentValue);
        } else {
          this.codes = Object(
            this.SmplPopupService.getMasterData(changes.apiUrl.currentValue)
          ).data;
          this.value = this.setName(this.value);
        }
      }
    }
    setTimeout(() => {
      setTimeout(() => {
        this.noChangeFlg = `OFF`;
      }, 1000);
    }, 0);
  }

  /**
   *
   *
   */
  public ngOnInit() {
    this.onInit();
    if (this.popupWidth) {
      this.extendWidth = this.popupWidth;
    }
    // //
    if (this.isDisabled) {
      if (this.SmplPopupService.getMasterData(this.apiUrl).length <= 0) {
        this.SmplPopupService.fetchAll(this.apiUrl).then((response) => {
          this.codes = response.data;
          this.codesHeader = Object.values(response.header);
        });
      }
    }
  }

  /**
   *api  
   */
  public async fetchCodes(url: string, value?: string) {
    if (this.isBusy) return;
    this.isBusy = true;
    this.lockScreenService.lockScreen();
    (document.activeElement as HTMLElement).blur();
    return Promise.resolve(this.SmplPopupService.fetchAll(url)).catch(_ => {this.lockScreenService.unlock()}).then(
      (response) => {
        this.codes = response.data;
        this.codesHeader = Object.values(response.header);
        this.dataTitle = response.title;
        const ok = this.checkValue(value || this.value);
        let result = "";
        if (ok) {
          if (value || this.value) {
            result = this.setName(value || this.value);
          }
          if (this.dataCache) {
            this.SmplPopupService.setMasterData(url, response);
          }
        }
        this.isBusy = false;

        return new Promise((resolve) => {
          resolve(result);
          this.lockScreenService.unlock();
        });
      }
    )

  }
  /**
   * onInput
   * @param event input
   */
  public onInput(event) {
    SHCtlCommon.replaceSpace(event);
    SHCtlCommon.replaceFullWidth(event);
    SHCtlCommon.replaceSign(event);
  }
  /**
   * 
   */
  public openDialog() {
    // value
    const tmp = this.value;
    if (
      !this.popupWidth &&
      this.codes.length != 0 &&
      Object.values(this.codes[0]).length >= 4
    ) {
      this.extendWidth = ShCtlConst.DIALOG_MAX_WIDTH;
    }
    this.dialogRef = this.dialog.open(SHCtlPopupDialogComponent, {
      height: ShCtlConst.DIALOG_HEIGHT,
      width: this.extendWidth,
      data: {
        dataTitle: this.dataTitle,
        codes: this.codes,
        codesHeader: this.codesHeader,
        hiddenColumns: this.hiddenColumns,
      },
    });
    this.dialogRef.afterClosed().subscribe((result) => {
      if (result) {
        this.setValueName(result);
        //this.allocateToInputValue(result);
        this.bindCtlService.setNoChangeFlg({
          bindColumnArray: this.bindColumnArray,
          record: result,
        });
        this.bindCtlService.setBindValue({
          bindColumnArray: this.bindColumnArray,
          record: result,
        });
        this.noChangeFlg = `ON`;
        //changeemitafterChange
        //emit
        const resultValues = Object.values(result);
        if (resultValues && resultValues.includes(tmp)) {
          this.valueChange.emit(this.value);
          return;
        }
        setTimeout(() => {
          this.dispatchOwnValueInputChangeEvent();
        }, 0);
      }
    });
  }
  /**
   *@ valueinput
   */
  private dispatchOwnValueInputChangeEvent() {
    if (this.ownValueInputElm) {
      this.ownValueInputElm.dispatchEvent(new Event("change"));
    }
  }
  /**
   *@ bindinputnull
   */
  public isBindInputNull(bindColumnArray: { [key: string]: string }[]) {
    if (!bindColumnArray || bindColumnArray.length == 0) return true;
    return bindColumnArray
      .map((bindColumnData) => {
        return document.querySelector(
          `input[name=${Object.keys(bindColumnData)[0]}]`
        ) as HTMLInputElement;
      })
      .map((bindInput) => {
        if (!bindInput) return false;
        return !!bindInput.value;
      })
      .includes(false);
  }

  /**
   * onClick
   * @param event click
   */
  public onClick(event) {
    this.onBeforeClick(event);

    setTimeout(() => {
      this.onAfterClick(event);
    }, 0);
  }

  /**
   * onFocus
   * @param event focus
   */
  public onFocus(event) {
    this.onBeforeFocus(event);

    event.target.select();

    setTimeout(() => {
      this.onAfterFocus(event);
    }, 0);
  }
  public clearAll() {
    this.value = "";
    this.name = "";
    if (this.ownLabelInputElm) {
      this.ownLabelInputElm.value = "";
    }
    this.valueChange.emit("");
  }
  /**
   * onChange
   * @param event change
   */
  public onChange(event) {
    this.onBeforeChange(event);
    if (!event.target.value) {
      this.clearAll();
    }
    let flg = false;
    if (this.noChangeFlg == "OFF") {
      flg = true;
      if (
        this.SmplPopupService.getMasterData(this.apiUrl).length == 0 &&
        event.target.value
      ) {
        this.noChangeFlg = "ON";
        this.fetchCodes(this.apiUrl, this.toText(event.target.value)).then(
          (value) => {
            this.valueChange.emit(value);
            event.target.value = this.toText(event.target.value);
            setTimeout(() => {
              this.onAfterChange(event);
            }, 0);
          }
        );
      } else {
        event.target.value = this.setName(event.target.value);
        this.valueChange.emit(event.target.value);
        setTimeout(() => {
          this.onAfterChange(event);
        }, 0);
      }
    }
    if (!flg) {
      setTimeout(() => {
        this.valueChange.emit(event.target.value);
        this.onAfterChange(event);
        setTimeout(() => {
          this.noChangeFlg = `OFF`;
        }, 1000);
      }, 0);
    }
  }

  /**
   * onBlur
   * @param event blur
   */
  public onBlur(event) {
    this.onBeforeBlur(event);

    setTimeout(() => {
      this.onAfterBlur(event);
    }, 0);
  }
  /**
   *
   */
  public checkValue(value) {
    if (!value) return false;
    //let matchedArray = [];
    if (this.codes[0]) {
      const filterResult = this.codes.filter(
        (element) =>
          //this.hasMatchRecords(element, value)
          Object.values(element)[this.bindColumnKey - 1] == value
      );
      if (filterResult.length == 0) {
        this.clearValue();
        this.valueChange.emit("");
        return false;
      }

      if (filterResult.length >= 2) {
        alert(value + "");
        this.clearValue();
        this.valueChange.emit("");
        return false;
      }
    }
    return true;
  }
  /**
   * 
   */
  public getCodes() {
    this.openDialog();
    if (this.SmplPopupService.getMasterData(this.apiUrl).length == 0) {
      this.SmplPopupService.fetchAll(this.apiUrl).then((response) => {
        this.codes = response.data;
        this.codesHeader = Object.values(response.header);
        this.dataTitle = response.title;
        if (this.dataCache)
          this.SmplPopupService.setMasterData(this.apiUrl, response);
        if (!this.dialogRef) return;
        if (this.dialogRef.componentInstance) {
          this.dialogRef.componentInstance.codesHeader = this.codesHeader;
          this.dialogRef.componentInstance.unchangeableCodes = this.codes;
          this.dialogRef.componentInstance.codes = this.codes;
          this.dialogRef.componentInstance.dataTitle = this.dataTitle;
          if (!this.popupWidth && Object.values(this.codes[0]).length >= 4) {
            this.extendWidth = ShCtlConst.DIALOG_MAX_WIDTH;
          }
          this.dialogRef.updateSize(this.extendWidth, ShCtlConst.DIALOG_HEIGHT);
          let cols = [];
          this.dialogRef.componentInstance
            .getHashKeys()
            .forEach((name, idx) => {
              if (!this.hiddenColumns.find((_) => _ == idx + 1))
                cols.push(name);
            });
          this.dialogRef.componentInstance.displayedColumns = cols;
        }
      });
    } else {
      this.codes = Object(
        this.SmplPopupService.getMasterData(this.apiUrl)
      ).data;
      this.codesHeader = Object.values(
        Object(this.SmplPopupService.getMasterData(this.apiUrl)).header
      );
      if (this.dialogRef.componentInstance) {
        this.dialogRef.componentInstance.codesHeader = this.codesHeader;
        this.dialogRef.componentInstance.unchangeableCodes = this.codes;
        this.dialogRef.componentInstance.codes = this.codes;
        this.dialogRef.componentInstance.dataTitle = this.dataTitle;
        if (!this.popupWidth && Object.values(this.codes[0]).length >= 4) {
          this.extendWidth = ShCtlConst.DIALOG_MAX_WIDTH;
        }
        this.dialogRef.updateSize(this.extendWidth, ShCtlConst.DIALOG_HEIGHT);
        let cols = [];
        this.dialogRef.componentInstance.getHashKeys().forEach((name, idx) => {
          if (!this.hiddenColumns.find((_) => _ == idx + 1)) cols.push(name);
        });
        this.dialogRef.componentInstance.displayedColumns = cols;
      }
    }
  }

  /**
   * input value 
   * @param matchedArray
   */
  public setValueName(matchedArray) {
    if (Object.values(matchedArray)[this.bindColumnKey - 1]) {
      const val = Object.values(matchedArray)[
        this.bindColumnKey - 1
      ].toString();
      this.value = val;
    } else {
      this.value = "";
    }
    if (Object.values(matchedArray)[this.bindColumnValue - 1]) {
      this.name = Object.values(matchedArray)[
        this.bindColumnValue - 1
      ].toString();
    } else {
      this.name = "";
    }
  }
  /**
   *input
   */
  private hasMatchRecords(element: any, val: any): boolean {
    // 
    let searchs = [];
    // 
    let values = [val];
    searchs.push(Object.values(element)[this.bindColumnKey - 1]);
    if (this.bindColumnArray && this.bindColumnArray.length != 0) {
      this.bindColumnArray.forEach((data) => {
        let bindIndex = (Object.values(data)[0] as number) - 1;
        searchs.push(Object.values(element)[bindIndex]);
        let targetInput = document.querySelector(
          `input[name=${Object.keys(data)[0]}]`
        ) as HTMLInputElement;
        if (targetInput) {
          if (targetInput.value) {
            values.push(targetInput.value);
          } else {
            searchs = searchs.filter(
              (val) => val != Object.values(element)[bindIndex]
            );
          }
        }
      });
    }
    const result = searchs.map((val) => values.includes(val));
    return !result.includes(false);
  }
  /**
   * 
   */
  public setName(val) {
    const record = this.codes
      ? this.codes.find(
          (element) => Object.values(element)[this.bindColumnKey - 1] == val
        )
      : null;
    if (record) {
      let kbnValue = Object.values(record)[this.bindColumnValue - 1];
      this.name = kbnValue ? kbnValue.toString() : "";
      this.bindCtlService.setBindValue({
        bindColumnArray: this.bindColumnArray,
        record,
      });
    } else {
      val = "";
      this.clearAll();
    }
    return val;
  }

  /**
   * 
   */
  public clearValue() {
    this.value = "";
    this.name = "";
    if (this.ctlName) {
      this.ownValueInputElm.value = "";
      //changeevent.target.value
      setTimeout(() => {
        this.ownValueInputElm.dispatchEvent(new Event("change"));
        this.ownValueInputElm.dispatchEvent(new Event("focus"));
      }, 0);
    }
    if (this.popupLabelName) {
      this.ownLabelInputElm.value = "";
    }
  }
  /**
   * 
   */
  public toText(s: string) {
    return s;
  }
  public extractBindValue(result: any) {
    if (!result) return;
    return Object.values(result)[this.bindColumnKey - 1];
  }
}
