import { Component, OnInit } from '@angular/core';
import { SHCtlCommon } from '../sh-ctl-common';
import { SHFormControl } from '../../utilities/ShFormControl'

@Component({
  selector: 'sh-ctl-tel-no',
  templateUrl: './sh-ctl-tel-no.component.html',
  styleUrls: ['./sh-ctl-tel-no.component.css', '../../../style.common.css']
})
export class SHCtlTelNoComponent extends SHFormControl<string> implements OnInit {
  public stringValue: any;

  constructor() {
    super();
    this.ctlName = "SHCtlTelNo";
    this.maxlength = 15;
  }

  public ngAfterContentChecked() {
    if (this.value != null) {
      this.stringValue = this.value;
    }
  }

  public ngOnInit() {
    this.onInit();
  }

  /**
   * onInput
   * @param event input
   */
  public onInput(event) {
    SHCtlCommon.replaceFullWidth(event);
    SHCtlCommon.sliceLength(event, this.maxlength);
  }

  /**
   * onClick
   * @param event click
   */
  public onClick(event) {
    this.onBeforeClick(event);

    this.onAfterClick(event);
  }

  /**
   * onChange
   * @param event change
   */
  public onChange(event) {
    this.onBeforeChange(event);

    this.valueChange.emit(event.target.value);

    setTimeout(() => {this.onAfterChange(event)}, 0);
  }

  /**
   * onFocus
   * @param event focus
   */
  public onFocus(event) {
    this.onBeforeFocus(event);

    event.target.select();

    setTimeout(() => {this.onAfterFocus(event)}, 0);
  }

  /**
   * onBlur
   * @param event blur
   */
  public onBlur(event) {
    this.onBeforeBlur(event);

    setTimeout(() => {this.onAfterBlur(event);}, 0);;
  }

}
