import { Component, OnInit, EventEmitter, Input, Output, OnChanges, SimpleChanges } from "@angular/core";
import { SmplKbnCodeType } from "../../entities/SmplKbnCodeType";
import { SmplKbnCodeService } from "../../services/smpl-kbn-code.service";
import { SHFormControl } from '../../utilities/ShFormControl'

@Component({
  selector: "sh-ctl-kbn-radio",
  templateUrl: "./sh-ctl-kbn-radio.component.html",
  styleUrls: ["./sh-ctl-kbn-radio.component.css", "../../../style.common.css"]
})
export class SHCtlKbnRadioComponent extends SHFormControl<string> implements OnInit, OnChanges {
  @Input() public dataKbnName: string = "";
  @Input() public radioContent: any = "";
  @Input() public apiUrl: string = "/api/";
  @Input() public displayLabel: string = "";
  @Output() public throwError = new EventEmitter();

  public name: string = "";
  public kbnCodes: SmplKbnCodeType[];

  constructor(private SmplKbnRadioService: SmplKbnCodeService) {
    super();
    this.ctlName = "SHCtlKbnRadio";
  }
 
  /**
   *
   *
   */
  public ngOnInit() {
    this.onInit();
    if (!this.items) {
      if (this.SmplKbnRadioService.getMasterData(this.apiUrl).length == 0) {
        this.SmplKbnRadioService.fetchAll(this.apiUrl).then(response => {
          this.kbnCodes = response;
          this.SmplKbnRadioService.setMasterData(this.apiUrl, response);
        });
      } else {
        if (this.SmplKbnRadioService.getMasterData(this.apiUrl).length > 0)
          this.kbnCodes = this.SmplKbnRadioService.getMasterData(this.apiUrl);
      }
    } else {
      this.kbnCodes = this.items;
    }
  }

  /**
   * ngOnChanges
   */
  public ngOnChanges(change: SimpleChanges) {
    if(change.items && change.items.currentValue) {
      this.kbnCodes = change.items.currentValue;
    }
    if (change.value) {
      if (change.value.currentValue) {
        this.value = change.value.currentValue.toString();
      }
    }
  }

  /**
   * onClick
   * @param event click
   */
  public onClick(event) {
    this.onBeforeClick(event);

    this.onAfterClick(event);
  }

  /**
   * onChange
   * @param event change
   */
  public onChange(event: any) {
    this.onBeforeChange(event);

    this.valueChange.emit(event.value);

    setTimeout(() => {this.onAfterChange(event)}, 0);
  }

  /**
   * onFocus
   * @param event focus
   */
  public onFocus(event) {
    this.onBeforeFocus(event);

    setTimeout(() => {this.onAfterFocus(event)}, 0);
  }

  /**
   * onBlur
   * @param event blur
   */
  public onBlur(event) {
    this.onBeforeBlur(event);

    setTimeout(() => {this.onAfterBlur(event);}, 0);;
  }

  /**
   * 
   * @param value this.value
   */
  public changeRadioContent(item) {
    this.radioContent = item;
  }
}
