import {
  Component,
  OnInit,
  Input,
  OnChanges,
  SimpleChanges,
  ElementRef,
  AfterViewInit
} from "@angular/core";
import { SHCtlCommon } from "../sh-ctl-common";
import { SHFormControl } from "../../utilities/ShFormControl";
import { FormControl } from "@angular/forms";
import {
  MomentDateAdapter,
  MAT_MOMENT_DATE_ADAPTER_OPTIONS
} from "@angular/material-moment-adapter";
import {
  DateAdapter,
  MAT_DATE_FORMATS,
  MAT_DATE_LOCALE,
  NativeDateAdapter
} from "@angular/material/core";
import { MatDatepicker } from "@angular/material/datepicker";
import * as _moment from "moment";
import { Moment } from "moment";

const moment = _moment;

type yearAndMonth = {
  year: number;
  month: number;
};

@Component({
  selector: "sh-ctl-year-month",
  templateUrl: "./sh-ctl-year-month.component.html",
  styleUrls: ["./sh-ctl-year-month.component.css", "../../../style.common.css"],
})
export class SHCtlYearMonthComponent extends SHFormControl<string>
  implements OnInit, OnChanges, AfterViewInit {
  public isValidateError: boolean = false;
  public errorMessage: string = "";
  public adjust: any;
  public stringValue: any;
  public cache: yearAndMonth = { year: null, month: null };
  public date = new FormControl(moment());
  @Input() public max = "9999-12";
  @Input() public min = "1000-12";
  constructor(private el: ElementRef) {
    super();
    this.ctlName = "SHCtlYearMonth";
  }

  public ngOnChanges(changes: SimpleChanges) {
    if (!changes.value) {
      return;
    }
    if (!changes.value.currentValue) {
      this.value = "";
      return;
    }
    if (changes.value.currentValue.length >= 7) {
      this.value = "";
      return;
    }
    if (changes.value.currentValue) {
      this.value = this.toText(changes.value.currentValue);
    }
  }

  public getYearAndManth(date: any) {
    if (!date) return;
    const val = new Date(date).toLocaleDateString().split('/')
    const year = val[0];
    const month = val[1];
    return `${year}${month}`;
  }

  public ngAfterContentChecked() {
    if (this.value != null && this.isDisabled) {
      this.changeFormat(this.value.replace("-", ""));
    }
  }

  public ngOnInit() {
    this.onInit();
  }

  public ngAfterViewInit() {
    super.ngAfterViewInit();
    if(!this.isDisabled) {
      let element = <HTMLElement>this.el.nativeElement.querySelector('input');
      element.setAttribute("max", this.max);
      element.setAttribute("min", this.min);
    }
  }

  /**
   * onOpened
   */
  public onOpened() {
    let el = document.querySelector("mat-datepicker-content");
    el.addEventListener("focusin", () => {
      this.isfocused = true;
    });
  }

  /**
   * onClosed
   */
  public onClosed() {
    let inputValue = <HTMLInputElement>document.querySelector(`textarea[name=` + this.ctlName + `], input[name=` + this.ctlName + `]`);
    setTimeout(function(){inputValue.dispatchEvent(new Event('focus'));},0);  
  }

  /**
   * onInput
   * @param event input
   */
  public onInput(event) {
    this.yearAndMonthCalculation(event);
  }

  /**
   * onKeypress
   * @param event keypress
   */
  public onKeypress(event) {
    this.yearAndMonthCalculation(event);
  }

  /**
   * onClick
   * @param event click
   */
  public onClick(event) {
    this.onBeforeClick(event);

    this.onAfterClick(event);
  }

  /**
   * onChange
   * @param event change
   */
  public onChange(event) {
    this.onBeforeChange(event);

    this.valueChange.emit(this.toValue(event.target.value));

    setTimeout(() => {this.onAfterChange(event)}, 0);
  }

  /**
   * onFocus
   * @param event focus
   */
  public onFocus(event) {
    this.onBeforeFocus(event);
   
    this.value = this.toText(event.target.value);
    event.target.select();

    setTimeout(() => {this.onAfterFocus(event)}, 0);
    return true;
  }

  /**
   * onBlur
   * @param event blur
   */
  public onBlur(event) {
    this.onBeforeBlur(event);

    setTimeout(() => {this.onAfterBlur(event);}, 0);;
  }

  /**
   * 
   * @param event 
   */
  private yearAndMonthCalculation(event) {
    let key = this.isKeysAllowedToMe(event);
    if (key === 0) {
      this.setCurrentDate(event);
      this.onChange(event);
    }
  }

  /**
   * 
   * @param event keypress
   */
  private isKeysAllowedToMe(event: any) {
    if (event.key === SHCtlCommon.Const.keyCodes.signs[191]) {
      return 0;
    }
    return null;
  }

  /**
   * 
   * @param event keypress
   */
  public setCurrentDate(event) {
    let newDate = new Date();
    let nullFirstString = null;
    let nullLastString = null;
    nullFirstString = SHCtlCommon.padding(newDate.getFullYear(), 4); // YYYY
    nullLastString = SHCtlCommon.padding(newDate.getMonth() + 1, 2); // MM
    event.target.value = String(nullFirstString + "-" + nullLastString);
  }

  /**
   * 
   * @param inputTargetValue num.substr(0, 2)
   */
  public convYear(inputTargetValue) {
    if (inputTargetValue >= "00" && inputTargetValue <= 49) {
      return "20" + inputTargetValue;
    } else {
      return "19" + inputTargetValue;
    }
  }

  /**
   * YYYY/MM
   * @param inputTargetValue event.target.value
   */
  public toText(inputTargetValue) {
    let num = inputTargetValue.toString().replace(/\//g, "");
    if (num.trim().length === 0) {
      return "";
    }
    // 4
    if (num.length === 3 || num.length === 4) {
      // // 
      return SHCtlCommon.padding(num, 4) + "-01";
    } else if (num.length === 5 || num.length === 6) {
      // 
      let year = num.substr(0, 4) + "-" + ("00" + num.substr(4)).slice(-2);
      return year;
    } else {
      return inputTargetValue;
    }
  }

  /**
   * 
   * @param inputTargetValue event.target.value
   */
  public toValue(inputTargetValue) {
    return new String(inputTargetValue).replace("-", "");
  }

  /**
   * 
   * @param value this.value
   */
  public changeFormat(value) {
    if (value != null && value != undefined && value.length === 6) {
      let nullFirstString = null;
      let nullLastString = null;
      nullFirstString = value.slice(0, 4);
      nullLastString = value.slice(4, 6);
      this.stringValue = String(nullFirstString + "" + nullLastString + "");
      return this.stringValue;
    } else {
      this.stringValue = '';
      return this.stringValue;
    }

  }
}