import { Directive, ElementRef, Input, OnInit } from '@angular/core';

@Directive({
  selector: '[ShFlex]'
})
export class ShFlexDirective implements OnInit {
  /**
   * 
   */
  @Input() colLg: number = 16.6
  private ctlLgFlex: string = `1 1 ${this.colLg}%`;
  private ctlLgMaxWidth: string = `${this.colLg}%`;
  /**
   * ipad ~ p
   */
  @Input() colMd: number = 33.3
  private ctlMdFlex: string = `1 1 ${this.colMd}%`;
  private ctlMdMaxWidth: string = `${this.colMd}%`;
  /**
   * mobile
   */
  @Input() colSm: number = 100
  private ctlSmFlex: string = `1 1 ${this.colSm}%`;
  private ctlSmMaxWidth: string = `${this.colSm}%`;
  //
  private mediaSm: string = "(max-width: 768px)"
  private mediaMd: string = "(max-width: 1024px)";
  //
  private ctlWidth: string = "100%";
  private ctlBoxSizing: string = "border-box";
  private ctlPadding: string = "0px 1rem 1rem 0px";

  constructor(private el: ElementRef) { }

  ngOnInit() {
    //
    //
    this.initParam();
    this.initStyle();
    window.addEventListener('resize', () => {
      this.initStyle()
    })
  }
  private initParam() {
    this.ctlLgFlex = `1 1 ${this.colLg}%`;
    this.ctlLgMaxWidth = `${this.colLg}%`;
    this.ctlMdFlex = `1 1 ${this.colMd}%`;
    this.ctlMdMaxWidth = `${this.colMd}%`;
    this.ctlSmFlex = `1 1 ${this.colSm}%`;
    this.ctlSmMaxWidth = `${this.colSm}%`;
    this.el.nativeElement.style.padding = this.ctlPadding;
    this.el.nativeElement.style.width = this.ctlWidth;
  }
  private initStyle() {
    const sm: MediaQueryList = window.matchMedia(this.mediaSm)
    const md: MediaQueryList = window.matchMedia(this.mediaMd)
    if (sm.matches) {
      this.smStyleInit()
    } else if (md.matches) {
      this.mdStyleInit()
    } else {
      this.defaultStyleInit()
    }
  }
  private smStyleInit() {
    this.el.nativeElement.style.padding = this.ctlPadding;
    this.el.nativeElement.style.width = this.ctlWidth;
    this.el.nativeElement.style.flex = this.ctlSmFlex;
    this.el.nativeElement.style.boxSizing = this.ctlBoxSizing;
    this.el.nativeElement.style.maxWidth = this.ctlSmMaxWidth;
  }
  private mdStyleInit() {
    this.el.nativeElement.style.padding = this.ctlPadding;
    this.el.nativeElement.style.width = this.ctlWidth;
    this.el.nativeElement.style.flex = this.ctlMdFlex;
    this.el.nativeElement.style.boxSizing = this.ctlBoxSizing;
    this.el.nativeElement.style.maxWidth = this.ctlMdMaxWidth;
  }
  private defaultStyleInit() {
    this.el.nativeElement.style.padding = this.ctlPadding;
    this.el.nativeElement.style.width = this.ctlWidth;
    this.el.nativeElement.style.flex = this.ctlLgFlex;
    this.el.nativeElement.style.boxSizing = this.ctlBoxSizing;
    this.el.nativeElement.style.maxWidth = this.ctlLgMaxWidth;
  }


}
